@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;400;600;700;900&family=Montserrat:wght@100;400;900&display=swap);
/* ——————————————————————————————————————————————————————————————
Variables 
—————————————————————————————————————————————————————————————— */
:root {
  --primary-color: #9308ba;
  --primary-dark-color: #8b09af;
  --secondary-color: #df8e67;
  --secondary-light-color: rgba(223, 143, 103, 0.3);
  --white-light-color: rgba(255, 255, 255, 0.6);
  --white-lighter-color: rgba(255, 255, 255, 0.3);

}

/* ——————————————————————————————————————————————————————————————
Base 
—————————————————————————————————————————————————————————————— */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: calc(100vh + 12px);
  background: #9308ba;
  background: var(--primary-color);
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.g__full-percent-height {
  height: 100%;
}

.g__main-wrapper {
  padding: 100px;
}

.g__sidebar-container {
    max-width: 250px;
}

.g__results-container {
  padding: 50px 50px 80px;
  border-radius: 15px;
  background:#9308ba;
  background:var(--primary-color);
  box-shadow:  16px 16px 32px #760695, 
             -16px -16px 32px #b00adf;

}


/* ——————————————————————————————————————————————————————————————
Typography 
—————————————————————————————————————————————————————————————— */

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: bolder;
  color: white;
  font-size: 1.8rem;
  text-transform: uppercase;
  line-height: 1.1;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-weight: bolder;
  color: white;
  font-size: 3rem;
  text-transform: uppercase;
  line-height: 1.1;
}

h4 {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.7rem;
  text-align: center;
  color: #df8e67;
  color: var(--secondary-color);
  font-weight: normal;
  margin-top: 4rem;
}

h5 {
  font-size: 1.2rem;
  text-align: center;
  color: white;
  font-weight: bold;
}


.g__temp-status-img {
  width: 30px;
  height: 100%;
}

.g__details-metric {
  font-size: 1rem;
  font-weight: 300;
  position: relative;
  top: -8px;
  opacity: .5;
  
}

.g__details-num {
  font-size: 3.5rem;
  font-weight: 200;
  color: white;
}

.g__details-label {
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 2px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  color: #df8e67;
  color: var(--secondary-color);
}








/* ——————————————————————————————————————————————————————————————
Bulma Overrides  
—————————————————————————————————————————————————————————————— */

.input,
.button {
    font-size: 16px;
    font-family: 'Inter', sans-serif;
    transition: .5s;
}

.input {
    padding-left: 20px; 
    background: #c379d8;
    border: none;
    color: white;
    font-weight: bold;
    border-right: white solid 3px;
    transition: .5s;
}

.input:hover {
    border-right: white solid 3px;
    transition: .5s;
}

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.657) !important; 
  font-weight: normal;
  
}

:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.657) !important; 
  font-weight: normal;
  
}

::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.657) !important; 
  font-weight: normal;
  
}

::placeholder {
  color: rgba(255, 255, 255, 0.657) !important; 
  font-weight: normal;
  
}

.input:focus {
  box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.35);
  border-color: white;
  font-size: 22px;
  transition: .5s;
}

.button {
  border: none;
  color: white;
  font-family: 'Inter', sans-serif;
  font-weight: bold;
  min-width: 120px;
  white-space: normal;
}

button {
  background: none;
  padding: 0;
  text-align: left;
  border: none;
  font: bold 1rem 'Inter', sans-serif;
}

button:focus {
  outline: white none 0;
  opacity: .6;

}


a:focus {
  outline: white none 0;
  opacity: .6;
}





/* ===========================================================
Mobile Styles  
=========================================================== */
@media screen and (max-width: 1110px) {
  .g__main-wrapper  {
    flex-direction: column;
  }
  .g__sidebar-container {
    max-width: 100%;
  }
  .g__main-wrapper {
    padding: 30px;
  }

} 


@media screen and (max-width: 768px) {
    
  .button {
    border: none;
    color: white;
    font-family: 'Inter', sans-serif;
    font-weight: bold;
    min-width: 70px;
    white-space: normal;
  }
  
  .input:focus {
    font-size: 18px;
  }

}

@media screen and (max-width: 475px) {
  .g__results-container {
    padding: 0;
  box-shadow:  none;

  }

  h2 {
    text-align: center;
    font-size: 1.8rem;
    text-transform: uppercase;
    line-height: 1.1;
  }
}
.search-bar__btn {
  background: rgb(168,87,209);
  background: linear-gradient(-90deg, #df8817, #a857d1, #9381ff);
  background-size: 800% 800%;

    -webkit-animation: bgColorChange 20s ease infinite;
    animation: bgColorChange 20s ease infinite;
}

@-webkit-keyframes bgColorChange {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  @keyframes bgColorChange {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  

.search-bar__btn:hover {
    opacity: .70;
    color: white;
    transition: .5s;
    
}

.search-bar__btn:focus {
    color: white;
    transition: .5s;
}

.search-bar__github-container {
    display: flex;
    justify-content: flex-end;
}

.search-bar__github {
    color: rgba(255, 255, 255, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(255, 255, 255, 0.5);
    height: 40px;
    font-size: 14px;
    max-width: 130px;
    border-radius: 4px;
    padding: 10px;
    transition: ease .3s;
}

.search-bar__github svg {
    margin-right: 8px;
}

.search-bar__github:hover {
    color: rgba(255, 255, 255, 0.7);
    background: var(--secondary-color);
    border-color: var(--secondary-color);
    transition: ease .3s;
}

@media screen and (max-width: 768px) {

    .search-bar__github-container {
        justify-content: center;
        padding: 0;
    }
    
    .search-bar__github {
        color: rgba(255, 255, 255, 0.7);
        border: none;
        height: 40px;
        font-size: 14px;
        max-width: 130px;

    }

    
      
}
.current-temp__container {
    margin-top: 50px;
    padding: 50px;
}

.current-temp__header {
    margin-bottom: 70px;
   
}

.current_temp__temp-sub {
    display: flex;
}

.current-temp__meta {
    display: flex;
    margin-top: 10px;
    align-items: center;
    color: rgba(255, 255, 255, 0.6);
    font-weight: 600;
}

.current-temp__meta-date {
    padding-right: 15px;
    margin-right: 10px;
    border-right: 2px solid rgba(255, 255, 255, 0.4);
}

.current-temp__meta-status {
    display: flex;
    align-items: center;
}


.current-temp__details-container {
text-align: center;
}

.current-temp__details-container .column {
border-top: var(--secondary-light-color) solid 1px;
border-left: var(--secondary-light-color) solid 1px;
border-bottom: var(--secondary-light-color) solid 1px;
padding: 40px 0 60px;
background: rgba(255, 255, 255, 0.05);
}

.current-temp__details-container .column:first-of-type {
background: rgba(255, 255, 255, 0.05);
}

.current-temp__details-container div:last-child {
border-right: var(--secondary-light-color) solid 1px;
}

.current-temp__blur {
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: hue-rotate(2deg);
    -webkit-backdrop-filter: hue-rotate(2deg);
}




@media screen and (max-width: 768px) {
    .current-temp__details-container div {
        border: var(--secondary-light-color) solid 1px;
        padding: 40px 0 60px;
        background: rgba(70, 2, 83, 0.2);
        }
}

@media screen and (max-width: 475px) {
    .current-temp__container {
        padding: 30px;
        margin-top: 30px;
    }

    .current-temp__meta {
        flex-direction: column;
    }

    .current-temp__meta-date {
        padding-right: 0;
        margin-right: 0;
        border: none;
    }

    .current-temp__meta-status {
        margin-right: 10px;
    }

    .current-temp__header {
        margin-bottom: 30px;
       
    }
}
.forecast__main {
    margin: 1rem auto 0;
    flex-wrap: wrap;

}

.forecast__container {
    border-radius: 15px;
    background: #9308ba;
    box-shadow:  7px 7px 25px #7d079e, 
    -7px -7px 25px #a909d6;
    min-width: 220px;
    max-width: 400px;
    margin: 0 auto;
    color: var(--secondary-color);
}

.forecast__header {
    background: var(--primary-dark-color);
    width: 100%;
    border-radius: 15px 15px 0 0;
    padding: 15px;
    margin-bottom: 20px;
}

.forecast__body {
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}


.forecast__meta-container {
    margin-top: 25px;
    padding-top: 10px;
    border-top: 1px solid #7d079e;
    width: 100%;
    text-align: center;

}

.forecast__meta-container h3{
    color: var(--secondary-color);

}

.forecast__meta-container sup{
    font-weight: 500;
    opacity: .8;

}





@media screen and (max-width: 1745px) {
    .forecast__container {
        max-width: 280px;
    
    }
}
@media screen and (max-width: 1110px) {
    
