}

.sidebar__logo--three {
    opacity: .65;
}

.sidebar__logo--four {
    opacity: .85;
}


@media screen and (max-width: 1110px) {
    
    .side-bar__logo {
       margin: 10px 0 40px;
       max-width: 100%;
       text-align: center;
       font-size:1.5rem;
    }

   
    
}
.search-error__container {
    width: 100%;
    text-align: center;
    margin-top: 50px;
    padding: 50px 30px;
    color: var(--secondary-color);
    border-radius: 15px;
    background: #9308ba;
    box-shadow: inset 9px 9px 18px #7d079e, 
                inset -9px -9px 18px #a909d6;
  }
  
  .search-error__title {
    color: var(--secondary-color);
  }
  
  .search-error__msg {
    max-width: 400px;
    margin: 10px auto 0;
    font-weight: 600;
  
  }
.pre-content__container {
  text-align: center;
    margin: 50px auto -30px;
    padding: 50px 30px;
    color: var(--secondary-color);
    border-radius: 15px;
    background: #9308ba;
    box-shadow:  7px 7px 25px #7d079e, 
    -7px -7px 25px #a909d6;
  }
  
  .pre-content__title {
    color: var(--secondary-color);
  }
  
  .pre-content__msg {
    max-width: 400px;
    margin: 10px auto 0;
    font-weight: 600;
  
  }
